<script>
import $ from 'jquery'
export default {
  name: 'MainBase',
  watch: {
    $route (to, from) {
      // console.log(to.name + '监听')
      this.setSelectMenuId(to.name, from.name)
    }
  },
  mounted () {
    var name = this.$route.name
    $('#' + name).addClass('active')
    // console.log(name)
    this.setSelectMenuId(name)
  },
  methods: {
    setSelectMenuId: function (toName, fromName) {
      const mainHeaderMenuList = [
        'main-header',
        'sidebar',
        'control-sidebar',
        'boxes',
        'info-box',
        'direct-chat'
      ]
      const jsMenuList = [
        'js-layout',
        'js-push-menu',
        'js-tree',
        'js-control-sidebar',
        'js-box-widget',
        'js-box-refresh',
        'js-todo-list'
      ]
      if (fromName) {
        $('#' + fromName).removeClass('active')
        if (mainHeaderMenuList.indexOf(fromName) > -1) {
          // $('#components').removeClass('menu-open')
          $('#components').removeClass('active')
        } else if (jsMenuList.indexOf(fromName) > -1) {
          // $('#javascript').removeClass('menu-open')
          $('#javascript').removeClass('active')
        }
      }
      if (toName) {
        $('#' + toName).addClass('active')
        if (mainHeaderMenuList.indexOf(toName) > -1) {
          $('#javascript').removeClass('menu-open')
          $('#components').addClass('menu-open')
          $('#components').addClass('active')
        } else if (jsMenuList.indexOf(toName) > -1) {
          $('#components').removeClass('menu-open')
          $('#javascript').addClass('menu-open')
          $('#javascript').addClass('active')
        }
      }
      console.log('setSelectMenuId 调用了' + toName)
    },
    handleClick (e) {
      console.log(e)
    }
  }
}
</script>

