<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Upgrade Guide</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="upgrade">
              <h3 id="from-2.3-to-2.4">From v2.3 to v2.4</h3>
              <h4>HTML Changes</h4>
              <ul>
                <li>Change <code>dist/js/app.min.js</code> to <code>dist/js/adminlte.min.js</code></li>
                <li>Add <code>data-widget="tree"</code> to the sidebar menu. Specifically, it should look like this: <pre><code>&lt;ul class="sidebar-menu" data-widget="tree"&gt;</code></pre></li>
                <li>Add <code>.treeview</code> to all <code>li</code> elements that contain sub menus in the sidebar menu. Example:
                  <pre><code>&lt;li class="treeview">
  &lt;a href="#">&lt;i class="fa fa-link">&lt;/i> &lt;span>Multilevel&lt;/span>
    &lt;span class="pull-right-container">
       &lt;i class="fa fa-angle-left pull-right">&lt;/i>
    &lt;/span>
  &lt;/a>
  &lt;ul class="treeview-menu">
    &lt;li>&lt;a href="#">Link in level 2&lt;/a>&lt;/li>
    &lt;li>&lt;a href="#">Link in level 2&lt;/a>&lt;/li>
  &lt;/ul>
&lt;/li></code></pre></li>
                <li>Change <code>data-toggle="offcanvas"</code> to <code>data-toggle="push-menu"</code></li>
                <li>
                  If you are using the Todo List plugins, change <code>$('.list').todolist()</code> to <code>$('.list').todoList()</code>
                  or use the new <code>data-widget="todo-list"</code>.
                </li>
                <li>
                  Box widget changes:
                  <ul>
                    <li>Use <code>$('.box').boxWidget(options)</code> instead of <code>$('.box').activateBox()</code></li>
                    <li>Use <code>$('.box').boxWidget('toggle')</code> instead of <code>$('.box').toggleBox()</code></li>
                    <li>Use <code>$('.box').boxWidget('remove')</code> instead of <code>$('.box').removeBox()</code></li>
                  </ul>
                </li>
                <li>BoxRefresh plugin has been recreated, please review <a href="/docs/js-box-refresh">the docs</a>.</li>
                <li>
                  To apply Google fonts, paste this code in the <code>&lt;head&gt;</code> section of your html files.
                  <pre><code>&lt;link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,600,700,300italic,400italic,600italic"></code></pre>
                </li>
              </ul>

              <h3 id="from-1-to-2">From v1.x to v2.x</h3>
              <h4>New Files</h4>
              <p>Make sure you update all CSS and JS files that are related to AdminLTE. Otherwise, the layout will not
                function properly. Most important files are AdminLTE.css, skins CSS files, and app.js.</p>
              <h4>Layout Changes</h4>
              <ol>
                <li>The .wrapper div must be placed immediately after the body tag rather than after the header</li>
                <li>Change the .header div to .main-header <code>&LT;div class="main-header"></code></li>
                <li>Change the .right-side class to .content-wrapper <code>&LT;div class="content-wrapper"></code></li>
                <li>Change the .left-side class to .main-sidebar <code>&LT;div class="main-sidebar"></code></li>
                <li>In the navbar, change .navbar-right to .navbar-custom-menu <code>&LT;div
                  class="navbar-custom-menu"></code></li>
              </ol>
              <h4>Navbar Custom Dropdown Menus</h4>
              <ol>
                <li>The icons in the notification menu do not need bg-* classes. They should be replaced with contextual
                  text color class such as text-aqua or text-red.
                </li>
              </ol>
              <h4>Login, Registration and Lockscreen Pages</h4>
              <p>There are major changes to the HTML markup and style to these pages. The best way is to copy the page's code
                and customize it.</p>
              <p>And you should be set to go!</p>
              <h4>Mailbox</h4>
              <p>Mailbox got an upgrade to include three different views. The views are inbox, read mail, and compose new
                email. To use these views,
                you should use the provided HTML files in the pages/mailbox folder.</p>
              <p><b class="text-red">Note:</b> the old mailbox layout has been deprecated in favor of the new one and will be
                removed by the next release.</p>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'upgrade-guide'
}
</script>

<style scoped>

</style>



