import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import installation from '@/components/articles/installation' // 安装
import dependencies from '@/components/articles/dependencies' // 依赖
import layout from '@/components/articles/layout' // 布局
import mainHeader from '@/components/articles/main-header' // 页眉
import sidebar from '@/components/articles/sidebar' // 侧边栏
import controlSidebar from '@/components/articles/control-sidebar' // 弹出侧边栏
import boxes from '@/components/articles/boxes' // 盒子
import infoBox from '@/components/articles/info-box' // 信息框
import directChat from '@/components/articles/direct-chat' // 即时聊天
import jsLayout from '@/components/articles/js-layout' // js布局
import jsPushMenu from '@/components/articles/js-push-menu' // 折叠菜单
import jsTree from '@/components/articles/js-tree' // 树
import jsControlSidebar from '@/components/articles/js-control-sidebar' // 控制边栏
import jsBoxWidget from '@/components/articles/js-box-widget' // 盒子小工具
import jsBoxRefresh from '@/components/articles/js-box-refresh' // 盒子刷新
import jsTodoList from '@/components/articles/js-todo-list' // 待办事项清单
import browserSupport from '@/components/articles/browser-support' // 浏览器兼容
import upgradeGuide from '@/components/articles/upgrade-guide' // 升级指南
import implementations from '@/components/articles/implementations' // 实例
import faq from '@/components/articles/faq' // FAQ
import license from '@/components/articles/license' // 许可协议

Vue.use(Router)

var router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/', // 安装
      name: 'installation',
      meta: {
        title: 'AdminLTE - 安装'
      },
      component: installation
    },
    {
      path: '/installation', // 安装
      name: 'installation',
      meta: {
        title: 'AdminLTE - 安装'
      },
      component: installation
    },
    {
      path: '/dependencies', // 依赖
      name: 'dependencies',
      meta: {
        title: 'AdminLTE - 依赖 & 插件'
      },
      component: dependencies
    },
    {
      path: '/layout', // 布局
      name: 'layout',
      meta: {
        title: 'AdminLTE - 布局'
      },
      component: layout
    },
    {
      path: '/main-header', // 页眉
      name: 'main-header',
      meta: {
        title: 'AdminLTE - 页眉'
      },
      component: mainHeader
    },
    {
      path: '/sidebar', // 页眉
      name: 'sidebar',
      meta: {
        title: 'AdminLTE - 侧边栏'
      },
      component: sidebar
    },
    {
      path: '/control-sidebar', // 弹出侧边栏
      name: 'control-sidebar',
      meta: {
        title: 'AdminLTE - 弹出侧边栏'
      },
      component: controlSidebar
    },
    {
      path: '/boxes', // 盒子
      name: 'boxes',
      meta: {
        title: 'AdminLTE - 盒子'
      },
      component: boxes
    },
    {
      path: '/info-box', // 信息框
      name: 'info-box',
      meta: {
        title: 'AdminLTE - 信息框'
      },
      component: infoBox
    },
    {
      path: '/direct-chat', // 即时聊天
      name: 'direct-chat',
      meta: {
        title: 'AdminLTE - 即时聊天'
      },
      component: directChat
    },
    {
      path: '/js-layout', // Layout 布局
      name: 'js-layout',
      meta: {
        title: 'AdminLTE - Layout 布局'
      },
      component: jsLayout
    },
    {
      path: '/js-push-menu', // 折叠菜单
      name: 'js-push-menu',
      meta: {
        title: 'AdminLTE - 折叠菜单'
      },
      component: jsPushMenu
    },
    {
      path: '/js-tree', // 树
      name: 'js-tree',
      meta: {
        title: 'AdminLTE - 树'
      },
      component: jsTree
    },
    {
      path: '/js-control-sidebar', // 控制边栏
      name: 'js-control-sidebar',
      meta: {
        title: 'AdminLTE - 控制边栏'
      },
      component: jsControlSidebar
    },
    {
      path: '/js-box-widget', // 盒子小工具
      name: 'js-box-widget',
      meta: {
        title: 'AdminLTE - 盒子小工具'
      },
      component: jsBoxWidget
    },
    {
      path: '/js-box-refresh', // 盒子刷新
      name: 'js-box-refresh',
      meta: {
        title: 'AdminLTE - 盒子刷新'
      },
      component: jsBoxRefresh
    },
    {
      path: '/js-todo-list', // 代办事项清单
      name: 'js-todo-list',
      meta: {
        title: 'AdminLTE - 代办事项清单'
      },
      component: jsTodoList
    },
    {
      path: '/browser-support', // 浏览器兼容
      name: 'browser-support',
      meta: {
        title: 'AdminLTE - 浏览器兼容'
      },
      component: browserSupport
    },
    {
      path: '/upgrade-guide', // 升级指南
      name: 'upgrade-guide',
      meta: {
        title: 'AdminLTE - 升级指南'
      },
      component: upgradeGuide
    },
    {
      path: '/implementations', // 实例
      name: 'implementations',
      meta: {
        title: 'AdminLTE - 实例'
      },
      component: implementations
    },
    {
      path: '/faq', // 实例
      name: 'faq',
      meta: {
        title: 'AdminLTE - FAQ'
      },
      component: faq
    },
    {
      path: '/license', // 许可协议
      name: 'license',
      meta: {
        title: 'AdminLTE - 许可协议'
      },
      component: license
    }
  ]
})
router.beforeEach((to, from, next) => {
  window.document.title = to.meta.title
  next()
})

router.afterEach((to, from, next) => {
  window.scrollTo(0, 0)
})

export default router



