<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Box Widget Plugin</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="js-box-widget">
              <p>
                The box widget plugin provides the functionality for collapsing, expanding and removing a box.
              </p>

              <h3>Usage</h3>
              <p>
                This plugin can be activated using the data-api or jQuery.
              </p>

              <h4>Data Api</h4>
              <p>
                This plugin provides two data-api attributes. Any element using one of the
                following attributes must be placed within the <code>.box-tools</code> div, which
                is usually in the box header. For more information about the box HTML structure,
                visit <a href="/docs/boxes">the box component documentation</a>
              </p>

              <h5><code>data-widget="collapse"</code></h5>
              <p>
                This attribute, when attached to a button, allows the box to be collapsed/expanded when
                clicked.
              </p>

              <div class="box">
                <div class="box-header with-border">
                  <h3 class="box-title">Collapsible Box Example</h3>
                  <div class="box-tools pull-right">
                    <!-- Collapse Button -->
                    <button type="button" class="btn btn-box-tool" data-widget="collapse">
                      <i class="fa fa-minus"></i>
                    </button>
                  </div>
                  <!-- /.box-tools -->
                </div>
                <!-- /.box-header -->
                <div class="box-body">
                  The body of the box
                </div>
                <!-- /.box-body -->
                <div class="box-footer">
                  The footer of the box
                </div>
                <!-- box-footer -->
              </div>
              <!-- /.box -->

              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/kLp2fnuy/2/embed/html/dark/"></script>-->
                <iframe width="100%" height="400" src="//jsfiddle.net/almasaeed2010/kLp2fnuy/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h5><code>data-widget="remove"</code></h5>
              <p>
                This attribute, when attached to a button, allows the box to be removed when clicked.
              </p>

              <div class="box">
                <div class="box-header with-border">
                  <h3 class="box-title">Removable Box Example</h3>
                  <div class="box-tools pull-right">
                    <!-- Remove Button -->
                    <button type="button" class="btn btn-box-tool" data-widget="remove">
                      <i class="fa fa-times"></i>
                    </button>
                  </div>
                  <!-- /.box-tools -->
                </div>
                <!-- /.box-header -->
                <div class="box-body">
                  The body of the box
                </div>
                <!-- /.box-body -->
                <div class="box-footer">
                  The footer of the box
                </div>
                <!-- box-footer -->
              </div>
              <!-- /.box -->

              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/trzwu8fs/embed/html/dark/"></script>-->
                <iframe width="100%" height="400" src="//jsfiddle.net/almasaeed2010/trzwu8fs/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h4>jQuery</h4>
              <p>
                To activate any button using jQuery, you must provide the removeTrigger and collapseTrigger options.
                Otherwise, the plugin will assume the default <code>data-widget</code> selectors.
              </p>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/uvd6p0v7/embed/js/dark/"></script>-->
                <iframe width="100%" height="214" src="//jsfiddle.net/almasaeed2010/uvd6p0v7/embedded/js/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h3>Options</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Type</th>
                  <th>Default</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>animationSpeed</td>
                  <td>Number</td>
                  <td>500</td>
                  <td>Slide animation speed in milliseconds</td>
                </tr>
                <tr>
                  <td>collapseTrigger</td>
                  <td>String</td>
                  <td>[data-widget="collapse"]</td>
                  <td>jQuery selector to the element responsible for collapsing the box</td>
                </tr>
                <tr>
                  <td>removeTrigger</td>
                  <td>String</td>
                  <td>[data-widget="remove"]</td>
                  <td>jQuery selector to the element responsible for removing the box</td>
                </tr>
                <tr>
                  <td>collapseIcon</td>
                  <td>String</td>
                  <td>fa-minus</td>
                  <td>The CSS class for the collapse icon</td>
                </tr>
                <tr>
                  <td>expandIcon</td>
                  <td>String</td>
                  <td>fa-plus</td>
                  <td>The CSS class for the expand icon</td>
                </tr>
                <tr>
                  <td>removeIcon</td>
                  <td>String</td>
                  <td>fa-times</td>
                  <td>The CSS class for the remove icon</td>
                </tr>
                </tbody>
              </table>

              <h3>Events</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>expanded.boxwidget</td>
                  <td>Triggered after the box is expanded.</td>
                </tr>
                <tr>
                  <td>collapsed.boxwidget</td>
                  <td>Triggered after the box is collapsed.</td>
                </tr>
                <tr>
                  <td>removed.boxwidget</td>
                  <td>Triggered after the box is removed.</td>
                </tr>
                </tbody>
              </table>
              <p>
                Example: <code>$('#my-box').on('expanded.boxwidget', handleExpandedEvent)</code>
              </p>

              <h3>Methods</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Method</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>toggle</td>
                  <td>Toggles the state of the box between expanded and collapsed</td>
                </tr>
                <tr>
                  <td>expand</td>
                  <td>Expands the box</td>
                </tr>
                <tr>
                  <td>collapse</td>
                  <td>Collapses the box.</td>
                </tr>
                <tr>
                  <td>remove</td>
                  <td>Removes the box.</td>
                </tr>
                </tbody>
              </table>
              <p>
                Example: <code>$('#my-box-widget').boxWidget('toggle')</code>
              </p>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'js-box-widget'
}
</script>

<style scoped>

</style>



