<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Todo List Plugin</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="js-todo-list">
              <p>
                The todo list plugin provides simple functionality the todo list box widget.
              </p>

              <h3>Usage</h3>
              <p>
                This plugin can be activated using the data-api or jQuery.
              </p>

              <h4>Data Api</h4>
              <p>
                Activate the plugin by adding <code>data-widget="todo-list"</code> to the ul element.
                If you need to provide onCheck and onUncheck methods, please use the jQuery API.
              </p>

              <h4>jQuery</h4>
              <p>
                The jQuery API provides more customizable options that allows the developer to
                handle checking and unchecking the todo list checkbox events.
              </p>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/92zs5jn0/2/embed/js/dark/"></script>-->
                <iframe width="100%" height="214" src="//jsfiddle.net/almasaeed2010/92zs5jn0/2/embedded/js/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h3>Options</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Type</th>
                  <th>Default</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>onCheck</td>
                  <td>Function</td>
                  <td>Anonymous Function</td>
                  <td>Handle checkbox oncheck event. The checkbox is passed as parameter to the function.</td>
                </tr>
                <tr>
                  <td>onUnCheck</td>
                  <td>Function</td>
                  <td>Anonymous Function</td>
                  <td>Handle checkbox onUnCheck event. The checkbox is passed as parameter to the function.</td>
                </tr>
                </tbody>
              </table>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'js-todo-list'
}
</script>

<style scoped>

</style>



