<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Browser Support</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="browsers">
              <div class="callout callout-warning">
                <h4>Note!</h4>
                <p>
                  IE9 does not support transitions or animations. The template will function properly but it won't
                  use animations/transitions on IE9.
                </p>
              </div>

              <p>AdminLTE supports the following browsers:</p>
              <ul>
                <li>IE9+</li>
                <li>Edge (latest)</li>
                <li>Firefox (latest)</li>
                <li>Safari (latest)</li>
                <li>Chrome (latest)</li>
                <li>Opera (latest)</li>
              </ul>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'browser-support'
}
</script>

<style scoped>

</style>



