<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Push Menu Plugin</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="js-push-menu">
              <p>
                The PushMenu plugin controls the toggle button of the main sidebar.
              </p>

              <h3>Usage</h3>
              This plugin can be activated as a jquery plugin or using the data api.

              <h4>Data API</h4>
              <p>
                Add <code>data-toggle="push-menu"</code> to a button to activate the plugin.
              </p>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/aroa9v6r/embed/html/dark/"></script>-->
                <iframe width="100%" height="88" src="//jsfiddle.net/almasaeed2010/aroa9v6r/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h4>JQuery</h4>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/evu6repL/embed/js/dark/"></script>-->
                <iframe width="100%" height="88" src="//jsfiddle.net/almasaeed2010/evu6repL/embedded/js/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h3>Options</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Type</th>
                  <th>Default</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>collapseScreenSize</td>
                  <td>Number</td>
                  <td>767</td>
                  <td>Screen width in pixels for small screens.</td>
                </tr>
                <tr>
                  <td>expandOnHover</td>
                  <td>Boolean</td>
                  <td>FALSE</td>
                  <td>Whether to activate the expand on hover effect.</td>
                </tr>
                </tbody>
              </table>


              <h3>Events</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Event Type</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>expanded.pushMenu</td>
                  <td>Fired when the sidebar expands.</td>
                </tr>
                <tr>
                  <td>collapsed.pushMenu</td>
                  <td>Fired when the sidebar collapses.</td>
                </tr>
                </tbody>
              </table>
              <p>
                Example: <code>$(document).on('expanded.pushMenu', handleExpandedEvent)</code>
              </p>

              <h3>Methods</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Method</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>toggle</td>
                  <td>Toggles the state of the menu between expanded and collapsed.</td>
                </tr>
                </tbody>
              </table>
              <p>
                Example: <code>$('[data-toggle="push-menu"]').pushMenu('toggle')</code>
              </p>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'js-push-menu'
}
</script>

<style scoped>

</style>



