<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">安装</h1>
        </div>
      </div>
    </header>
    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="installation">
              <p>
                可以使用多种方法安装AdminLTE。 从下面的列表中选择您最喜欢的方法。
                在开始之前，请务必先检查<a href="https://adminlte.io/adminlte-docs/%7Bv%7D/dependencies">依赖项部分</a>
                。
              </p>
              <h3>下载</h3>
              <h4>从 Github</h4>
              <p>访问 <a href="https://github.com/almasaeed2010/AdminLTE/releases">Github 发行版</a> 并下载最新版本.</p>
              <p>
                <a href="https://github.com/almasaeed2010/AdminLTE/releases" class="btn btn-primary" target="_blank">
                  下载最新版本
                </a>
              </p>
              <h3>命令行</h3>
              <h4>通过 NPM</h4>
              <p>
              </p><pre><code>npm install admin-lte --save</code></pre>
              <p></p>
              <h4>通过 Bower</h4>
              <p>
              </p><pre><code>bower install admin-lte</code></pre>
              <p></p>
              <p>如果 bower 询问使用哪个版本的 jQuery，请选择版本 3 或更高版本。</p>
              <h4>通过 Composer</h4>
              <p>
              </p><pre><code>composer require "almasaeed2010/adminlte=~2.4"</code></pre>
              <p></p>
              <h4>通过 Git</h4>
              <ul>
                <li>存储仓库 (<a href="https://help.github.com/articles/fork-a-repo/">链接</a>)。</li>
                <li>
                  拷贝到本地。
                  <pre><code>git clone https://github.com/YOUR_USERNAME/AdminLTE.git</code></pre>
                </li>
              </ul>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'installation'
}
</script>

<style scoped>

</style>



