    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="sidebar">
              <p>
                此页面左侧使用了 Sidebar（侧边栏）可以作为一个 Sidebar 的示例。构建一个侧边栏：
              </p>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/7Lr5td0e/embed/html/dark/"></script>-->
                <iframe width="100%" height="400" src="//jsfiddle.net/almasaeed2010/7Lr5td0e/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <p>有关侧边栏 JS 部分的更多信息，请访问 <a href="/js-push-menu">PushMenu 插件页面</a></p>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'sidebar'
}
</script>

<style scoped>

</style>



