<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Main Header（页眉）组件</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
