                <p>
                  AdminLTE 使用了 Bootstrap 3 的所有组件。你可以通过查看 <a href="http://getbootstrap.com">Bootstrap 文档</a>
                  来了解本文档所<b>没有</b>涉及到的其他组件。
                </p>
              </div>
              <div class="callout callout-info">
                <h4>贴士！</h4>
                <p>
                  如果您浏览示例页面并想要复制组件，请右键单击该组件并选择“检查元素”比搜索 HTML 页面更快的获得 HTML。
                </p>
              </div>
              <h3 id="component-main-header">Main Header（页眉）</h3>
              <p>Main Header 包含 logo 和导航栏。导航栏的构造与 Bootstrap 略有不同，因为它具有 Bootstrap 没有的组件。
                导航栏可以通过两种方式构建。
                下面是我们提供的标准导航栏的示例</p>
              <div class="example" id="example1">
                <!--<script async src="//jsfiddle.net/almasaeed2010/skmfLntd/5/embed/html,result/dark/"></script>-->
