              </div>
              <h4>顶部导航布局。Main Header 示例。</h4>
              <div class="callout callout-info">
                <h4>提醒！</h4>
                <p>要使用此 Main Header 而不是常规标题，必须将 <code>layout-top-nav</code> class 添加到 body 标签中。</p>
              </div>
              <p>
                顶部导航栏示例可在此
                <a href="https://almsaeedstudio.com/themes/AdminLTE/pages/layout/top-nav.html">演示页面中</a>
                找到。
              </p>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/skmfLntd/3/embed/html,result/dark/"></script>-->
                <iframe width="100%" height="400" src="//jsfiddle.net/almasaeed2010/skmfLntd/3/embedded/html,result/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

