<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Tree Plugin</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="js-tree">
              <p>
                The tree plugin converts a nested list into a tree view where sub menus can be expanded.
              </p>

              <h3>Usage</h3>
              This plugin can be activated using the data-api or jQuery.

              <h4>Data Api</h4>
              <p>
                Add <code>data-widget="tree"</code> to any <code>ul</code> or <code>ol</code> element to activate the plugin.
                All options can be passed using the data api. Example: <code>data-animation-speed="1000"</code>.
              </p>

              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/6y9n4kx8/embed/html/dark/"></script>-->
                <iframe width="100%" height="232" src="//jsfiddle.net/almasaeed2010/6y9n4kx8/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h4>jQuery</h4>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/5eL95awa/embed/js/dark/"></script>-->
                <iframe width="100%" height="88" src="//jsfiddle.net/almasaeed2010/5eL95awa/embedded/js/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h3>Options</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Type</th>
                  <th>Default</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>animationSpeed</td>
                  <td>Number</td>
                  <td>500</td>
                  <td>Speed of slide down/up animation in milliseconds.</td>
                </tr>
                <tr>
                  <td>accordion</td>
                  <td>Boolean</td>
                  <td>TRUE</td>
                  <td>Whether to collapse the open menu when expanding another.</td>
                </tr>
                <tr>
                  <td>followLink</td>
                  <td>Boolean</td>
                  <td>FALSE</td>
                  <td>Whether to follow the link in the <code>a</code> element instead of expanding the sub menu.</td>
                </tr>
                <tr>
                  <td>trigger</td>
                  <td>String</td>
                  <td><code>.treeview a</code></td>
                  <td>
                    Selector of the element that should respond to the click and result in expanding or collapsing it
                    sibling sub menu.
                  </td>
                </tr>
                </tbody>
              </table>

              <h3>Events</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>expanded.tree</td>
                  <td>Triggered after a sub menu expands.</td>
                </tr>
                <tr>
                  <td>collapsed.tree</td>
                  <td>Triggered after a sub menu collapses.</td>
                </tr>
                </tbody>
              </table>
              <p>
                Example: <code>$('ul').on('expanded.tree', handleExpandedEvent)</code>
              </p>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'js-tree'
}
</script>

<style scoped>

</style>



