<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Control Sidebar Component</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="control-sidebar">
              <p>
                Control sidebar is the right side bar. It can be used for many purposes and is extremely easy
                to create. The sidebar ships with two different show/hide styles. The first allows the sidebar to
                slide over the content. The second pushes the content to make space for the sidebar. Either of
                these methods can be set through the <a href="/docs/js-control-sidebar">Javascript options</a>.
              </p>
              <p>
                The following code should be placed within the <code>.wrapper</code> div. I prefer
                to place it right after the footer.
              </p>
              <h4>Dark Sidebar Markup</h4>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/n0pm9r90/embed/html/dark/"></script>-->
                <iframe width="100%" height="196" src="//jsfiddle.net/almasaeed2010/n0pm9r90/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h4>Light Sidebar Markup</h4>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/9cmr68ud/embed/html/dark/"></script>-->
                <iframe width="100%" height="196" src="//jsfiddle.net/almasaeed2010/9cmr68ud/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <p>
                Once you create the sidebar, you will need a toggle button to open/close it.
                By adding the attribute <code>data-toggle="control-sidebar"</code> to any button, it will
                automatically act as the toggle button.
              </p>

              <h4>Sidebar Toggle Markup</h4>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/e6uxk1mt/embed/html/dark/"></script>-->
                <iframe width="100%" height="105" src="//jsfiddle.net/almasaeed2010/e6uxk1mt/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'control-sidebar'
}
</script>

<style scoped>

</style>



