<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Control Sidebar Plugin</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="js-control-sidebar">
              <p>
                The control sidebar component is part of AdminLTE's layout as the right sidebar.
              </p>

              <h3>Usage</h3>
              <p>
                This plugin can be activated using the data-api or jQuery. To activate the plugin, you must
                first add the HTML markup to your layout, then create the toggle button as shown below.
              </p>

              <h4>HTML Markup</h4>
              <div id="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/bofyg56q/embed/html/dark/"></script>-->
                <iframe width="100%" height="214" src="//jsfiddle.net/almasaeed2010/bofyg56q/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h4>Data Api</h4>
              <p>
                Add <code>data-toggle="control-sidebar"</code> to any <code>button</code> or
                <code>a</code> element to activate the plugin.
                All options can be passed using the data api. Example: <code>data-slide="false"</code>.
              </p>

              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/vqoh7ebj/embed/html/dark/"></script>-->
                <iframe width="100%" height="88" src="//jsfiddle.net/almasaeed2010/vqoh7ebj/embedded/html/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h4>jQuery</h4>
              <p>
                Just like all other AdminLTE plugins, you can also activate the toggle button using jQuery by
                running the following example.
              </p>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/xfokfkf9/embed/js/dark/"></script>-->
                <iframe width="100%" height="88" src="//jsfiddle.net/almasaeed2010/xfokfkf9/embedded/js/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h3>Options</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Type</th>
                  <th>Default</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>Slide</td>
                  <td>Boolean</td>
                  <td>true</td>
                  <td>Whether the sidebar should slide over the content or push the content to make space for itself.</td>
                </tr>
                </tbody>
              </table>

              <h3>Events</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>expanded.controlsidebar</td>
                  <td>Triggered after the control sidebar is expanded.</td>
                </tr>
                <tr>
                  <td>collapsed.controlsidebar</td>
                  <td>Triggered after the control sidebar is collapsed.</td>
                </tr>
                </tbody>
              </table>
              <p>
                Example: <code>$('#toggle-button').on('expanded.controlsidebar', handleExpandedEvent)</code>
              </p>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'js-control-sidebar'
}
</script>

<style scoped>

</style>



