<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Layout Plugin</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="js-layout">
              <p>
                The layout plugin manages the layout in case of css failure to reset the height or width of the
                content.
              </p>

              <h3>Usage</h3>
              This plugin is activated automatically upon window load.

              <h3>Options</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Type</th>
                  <th>Default</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>slimscroll</td>
                  <td>Boolean</td>
                  <td>TRUE</td>
                  <td>Whether to activate slimScroll for the sidebar.</td>
                </tr>
                <tr>
                  <td>resetHeight</td>
                  <td>Boolean</td>
                  <td>TRUE</td>
                  <td>
                    Whether to reset height of the body to auto. You'd set this false if your main content div is
                    scrollable rather than the body.
                  </td>
                </tr>
                </tbody>
              </table>

              <h3>Methods</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Method</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>fix</td>
                  <td>Fix the content part of the page.</td>
                </tr>
                <tr>
                  <td>fixSidebar</td>
                  <td>Fix the sidebar part of the page.</td>
                </tr>
                </tbody>
              </table>
              <p>
                Example: <code>$('body').layout('fix')</code>
              </p>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'js-layout'
}
</script>

<style scoped>

</style>



