<template>
  <article>
    <!-- Content Header (Page header) -->
    <header class="content-header container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <h1 class="content-max-width">Box Refresh Plugin</h1>
        </div>
      </div>
    </header>

    <!-- Main content -->
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-8">
          <section class="content-max-width">
            <section id="js-box-refresh">
              <p>
                The box refresh plugin provides the functionality for loading ajax content into the box.
              </p>

              <h3>Usage</h3>
              <p>
                This plugin can be activated using the data-api or jQuery.
              </p>

              <h4>Data Api</h4>
              <p>
                Activate the plugin by adding <code>data-widget="box-refresh"</code> to the box and provide
                the required <code>data-source="/URL-TO-CONTENT"</code> option. By doing that, the plugin
                will automatically create a GET request to the provided URL and render the returned
                response the <code>.box-body</code> section of the box. If you need to process
                the returned response before rendering, you should use the jQuery API, which
                provides hooks to deal with the response.
              </p>

              <h4>jQuery</h4>
              <p>
                The jQuery API provides more customizable options that allows the developer to
                pre-process the request before rendering and post-process it after rendering.
              </p>
              <div class="example">
                <!--<script async src="//jsfiddle.net/almasaeed2010/prsak4eu/embed/js/dark/"></script>-->
                <iframe width="100%" height="400" src="//jsfiddle.net/almasaeed2010/prsak4eu/embedded/js/dark/" allowfullscreen="allowfullscreen" allowpaymentrequest frameborder="0"></iframe>
              </div>

              <h3>Options</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Type</th>
                  <th>Default</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>source</td>
                  <td>String</td>
                  <td>''</td>
                  <td>The URL to the source</td>
                </tr>
                <tr>
                  <td>params</td>
                  <td>Object</td>
                  <td>{}</td>
                  <td>GET query paramaters (example: {search_term: 'layout'}, which renders to URL/?search_term=layout)</td>
                </tr>
                <tr>
                  <td>trigger</td>
                  <td>String</td>
                  <td>.refresh-btn</td>
                  <td>The CSS selector to the refresh button</td>
                </tr>
                <tr>
                  <td>content</td>
                  <td>String</td>
                  <td>.box-body</td>
                  <td>The CSS selector to the target where the content should be rendered. This selector should exist within the box.</td>
                </tr>
                <tr>
                  <td>loadInContent</td>
                  <td>Boolean</td>
                  <td>TRUE</td>
                  <td>Whether to automatically render the content</td>
                </tr>

                <tr>
                  <td>onLoadStart</td>
                  <td>Function</td>
                  <td>Anonymous Function</td>
                  <td>Called before the ajax request is made</td>
                </tr>
                <tr>
                  <td>responseType</td>
                  <td>String</td>
                  <td>''</td>
                  <td>Response type (example: 'json' or 'html')</td>
                </tr>
                <tr>
                  <td>overlayTemplate</td>
                  <td>String</td>
                  <td>TRUE</td>
                  <td>The HTML template for the ajax spinner</td>
                </tr>
                <tr>
                  <td>onLoadDone</td>
                  <td>Function</td>
                  <td>Anonymous Function</td>
                  <td>Called after the ajax request is made. A
                    <code>response</code> parameter is passed to the function that hold the server response.
                  </td>
                </tr>
                </tbody>
              </table>

              <h3>Methods</h3>
              <table class="table table-striped table-bordered">
                <thead>
                <tr>
                  <th>Method</th>
                  <th>Description</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                  <td>load</td>
                  <td>Reloads the content and runs the onLoadStart and onLoadDone hooks</td>
                </tr>
                </tbody>
              </table>
              <p>
                Example: <code>$('#my-box-widget').boxRefresh('load')</code>
              </p>
            </section>
          </section>
        </div>
        <!-- 广告位 参看 \archive\ad.html -->
      </div>
    </div>
    <!-- /.content -->
  </article>
</template>

<script>
export default {
  name: 'js-box-refresh'
}
</script>

<style scoped>

</style>



